/* **************** ARTICLE SOCIALS ****************** */
.article-socials {
  display: grid;
  grid-gap: 30px;
  grid-template-columns: 100px 100px;
}
.socials-item {
  background: #00acee;
  cursor: pointer;
  display: inline-block;
  height: 110px;
  position: relative;
  transition: transform 0.2s ease-in-out;
  width: 100px;
}
.socials-item:hover {
  transform: scale(0.9);
}
.socials-item:active {
  transform: scale(0.85);
}
.socials-item .social-icon {
  color: #fff;
  font-size: 63px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.dr {
  background: #ed4a89;
}
.gl {
  background: #d6492e;
}
.fb {
  background: #3b589c;
}
